.list-items {
  background-color: #fff;
  padding: 0 0 1rem 0;
  border-radius: 0.5rem;
  overflow: hidden;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 1px 1px 2px 0px rgba(116, 116, 116, 0.19);
  /* border: 1px solid; */
}

.header {
  background-color: var(--green-color);
  /* background-color: rgb(236, 240, 17); */
  height: 4rem;
  display: -ms-flexbox !important;
  display: flex !important;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;

  font-family: "Work Sans";
  text-transform: uppercase;
  font-weight: 300;
  color: var(--purple-color);
  /* border: 1px solid; */
}
.header-date {
  font-weight: 300;
  font-size: 0.7rem;
  color: rgb(172, 126, 247);
}
.list-items ul {
  width: 100%;
  padding: 0;
  margin-top: 0;
}
.list-items ul li {
  font-weight: 400;
  list-style: none;
  text-align: left;
  /* border: 1px solid; */
}
.list-items ul li:nth-child(odd) {
  background-color: rgb(241, 255, 245);
}

.items_line {
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 3fr 1.5fr 1.5fr;
  -ms-flex-align: center;
      align-items: center;
  font-size: 0.85rem;
  text-transform: capitalize;
  color: rgb(80, 80, 80);
  height: auto;
  /* border: 1px solid; */
}
.items_line-name {
  text-align: left;
  padding: 1.8rem;
  /* border: 1px solid; */
}
.items_line-qtt {
  text-align: right;
  padding-right: 2rem;
  /* border: 1px solid; */
}
.items_line-icon {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  /* border: 1px solid; */
}
.items_line-icons {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  /* border: 1px solid; */

  font-size: 1.2rem;
  color: var(--green-color);
}
.items_line-icons > i {
  /* border: 1px solid; */
  height: auto;
  width: 100%;
  margin: 0 0.1rem;
}
.icon_check {
  font-size: 1.5rem;
  margin-left: 1.2rem;
  color: var(--light-blue-color);
  -webkit-transition: color 200ms ease-in-out;
  -o-transition: color 200ms ease-in-out;
  transition: color 200ms ease-in-out;
}
.icon_check:hover {
  cursor: pointer;
  color: var(--light-purple-color);
}
.icon_check-checked {
  color: var(--dark-green-color);
}



