.lists__array {
  position: relative;
  height: 100%;
}
.list_item {
  position: relative;
  width: 100%;
  height: 0;
  margin: 0.5rem 0;
  padding: 0.8rem;

  background-color: #fff;
  border-radius: 0.5rem;
  -webkit-box-shadow: 1px 1px 3px 0px rgba(116, 116, 116, 0.19);
          box-shadow: 1px 1px 3px 0px rgba(116, 116, 116, 0.19);

  display: grid;
  grid-template-rows: 1fr 0.5fr;
  grid-template-columns: 80% 20%;
  grid-template-areas:
    "title icon"
    "date icon";
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;

  -webkit-transition: all 500ms;

  -o-transition: all 500ms;

  transition: all 500ms;

  opacity: 0;
}
.list_item:hover {
  -webkit-box-shadow: 3px 3px 3px 0px rgba(116, 116, 116, 0.19);
          box-shadow: 3px 3px 3px 0px rgba(116, 116, 116, 0.19);
  -webkit-transform: scale(1.01);
      -ms-transform: scale(1.01);
          transform: scale(1.01);
  /* background-color: #f9fff9; */
}

.list_item:before {
  content: "";
  position: absolute;
  height: 100%;
  width: 10px;
  /* border: 1px solid; */
  /* background-color: rgb(247, 243, 26); */
  background-color: var(--purple-color);
  border-top-left-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
}

.part {
  /* border: 1px solid; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 1.5rem;

  color: rgb(85, 75, 95);
}
.title {
  height: auto;
  line-height: 0.2rem;
  text-transform: uppercase;
  font-weight: 100 !important;
  grid-area: title;
}
.title h2 {
  font-weight: 400 !important;
  font-size: 0.9rem;
}
.date {
  grid-area: date;
  -ms-flex-item-align: start;
      align-self: flex-start;
  font-weight: 100;
  font-size: 0.7rem;
}
.icon {
  grid-area: icon;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 1.2rem;
  color: var(--green-color);

  padding: 0;
  /* border: 1px solid; */
  height: 100%;
}
.icon > i {
  /* border: 1px solid; */
  height: auto;
  width: 100%;
  margin: 0 0.3rem;
}

.appear {
  opacity: 1;
  height: 70px;
}



