.add__item__box {
  display: grid !important;
  grid-template-rows: repeat(1fr);
  grid-template-columns: 70% 30%;
  grid-template-areas:
    "name btn_send"
    "date btn_send";
  background-color: var(--green-color);
}

.add__item__box input {
  background-color: transparent;
  border: 0;
  color: #fff;
  width: 90%;
}

.form-input {
  /* margin: 0.5rem 0; */

  background-color: #73ecc4;
  width: 90%;
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  font-size: 0.9rem;
  letter-spacing: 0.03rem;
  border-radius: 0.3rem;
}
.form-input input::-webkit-input-placeholder {
  color: #fff;
  letter-spacing: 0.03rem;
}
.form-input input::-ms-input-placeholder {
  color: #fff;
  letter-spacing: 0.03rem;
}
.form-input input::placeholder {
  color: #fff;
  letter-spacing: 0.03rem;
}

.list__add_name {
  grid-area: name;
  -ms-flex-item-align: end;
      align-self: flex-end;
  margin-left: 2rem;
}
.list__add_date {
  grid-area: date;
  -ms-flex-item-align: start;
      align-self: flex-start;
  margin-top: 0.5rem;

  margin-left: 2rem;
}
.list__add_send {
  position: relative;
  grid-area: btn_send;
  height: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 0.7rem;
  color: #73ecc4;
  background-color: transparent;
  z-index: 100;
}

.list__add_send::after {
  content: "";
  position: absolute;
  display: block;
  width: 3.5rem;
  height: 3.5rem;

  background-color: #fff;
  border-radius: 50%;
  z-index: -10;
}

.add__item__box > * {
  /* border: 1px solid #fff; */
}

.icons {
  color: var(--green-color);
  margin: 0 1rem;
}



