.menu {
  position: relative;
  width: 100%;
  height: 100%;

  display: grid;
  grid-template-rows: 4rem 12rem;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-areas:
    "row1col1 row1col2 row1col3 row1col4"
    "row2 row2 row2 row2";
}
.grid-cols {
  /* border: 1px solid #fff; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.row1col1 {
  grid-area: row1col1;

  background: var(--light-purple-color);
}
.row1col2 {
  grid-area: row1col2;
  background: var(--light-purple-color);
}
.row1col3 {
  grid-area: row1col3;
  background: var(--light-purple-color);
}
.row1col4 {
  grid-area: row1col4;
  background: var(--light-purple-color);
}
.row2 {
  grid-area: row2;
  /* border: 1px solid; */
  height: 100%;
}

.menuHide {
  display: none !important;
}
.menuShow {
  display: -ms-flexbox !important;
  display: flex !important;
}



