.login {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.login_call {
  width: 100%;
  font-size: 3rem;
  /* font-weight: bold; */
  font-weight: 100;

  text-align: center;
  color: #f9f9f9;
  padding: 3rem 0;
  /* border: 1px solid; */
}

.login__area {
  margin: 0 1rem;
  width: 370px;
  min-height: 300px;
  background-color: #f9f9f9;
  border-radius: 0.2rem;
  
  /*border: 3px solid;*/

  display: -ms-flexbox;

  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

  padding: 4rem 0;

  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 3px 3px 5px 0px rgba(116, 116, 116, 0.19);
}

.login__area section {
  display: grid;
  grid-template-rows: 4fr 1fr;
  margin-top: 2rem;
}

.login__area form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: auto;
  width: 90%;
}

.login__area form input {
  border: 0;
  height: 2.5rem;
  padding: 0.5rem;
  width: 70%;
  margin: 0.5rem 0;


}

.btn-login {
  border: 0;
  background-color: #79f8cd;
  border-radius: 0.2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-decoration: none;
  height: 3rem;
  max-width: 15rem;
  width: 90%;
  color: #3a3a3a;
  margin-top: 0.8rem;

  -webkit-box-shadow: 0 0.05rem 1px 0px rgba(116, 116, 116, 0.3);

          box-shadow: 0 0.05rem 1px 0px rgba(116, 116, 116, 0.3);
}



